

.header-color{
  background: #005AA7;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FFFDE4, #005AA7);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FFFDE4, #005AA7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.mdl-layout .mdl-layout__drawer-button{
  color: darkgrey;
}
.landing-grid{
  background: #005AA7;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FFFDE4, #005AA7);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FFFDE4, #005AA7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.about-me{
  width: 75%;
  margin: auto;
}
.projects-grid {
  display: flex;
}
img{
  border-radius: 50%;
}
.resume-right-col{
  background: #27221F;
  padding: 2em;
  color: rgb(201, 199, 199);
}
/* contact page css */
.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height:100%;
  justify-content: center;
  padding-top: 2em;
}
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;  
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: "Anton", sans-serif;
}

.contact-grid p{
  font-family: "oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.avatar-img {
  /*height: 250px;*/
  padding-top: 5em;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: grey;
}
.banner-text hr{
  border-top: 5px dotted grey;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: grey;
  font-size: 20px;
  padding: 1em;
}
.social-links{
  display: flex;
  justify-content: space-evenly;
  width: 50%;
  margin: auto;
}

.contact-list i{
  font-size: 60px;
  padding-right: 5rem;
}

.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;
}